import './App.scss';
import { Routes, Route, useNavigate, useLocation } from 'react-router-dom';
import { Login, Logout, Register, NotFound404, MainPage, Profile, ProfileEdit, Battles, Support, FAQ, AccountRestore, PostUpload, Container, Track, SavedPosts, Notifications, AccountRestoreEmailCheck, AccountVerify } from './pages/pages';
import { useEffect, useState } from 'react';
import { useCookies } from 'react-cookie';
import { store } from './redux/store';
import { id } from './components/baseSlice';
import { unwrapResult } from '@reduxjs/toolkit';
import { createTheme, CssBaseline, ThemeProvider } from '@mui/material';
import { useSelector } from 'react-redux';


function App() {
  //const currentUser = useSelector(state => state.base.user);
  const [cookies, , ] = useCookies(['user']);
  const navigate = useNavigate();
  const themeStyle = useSelector(state => state.base.theme);
  const location = useLocation();
  
  const [regAllowed] = useState(/\/(profile|track|register|account-restore|account-verify|battles|support|logout)\/*/);


  // theme setup
  const theme = createTheme({
    palette: {
      mode: themeStyle,
    },
  });

  useEffect(() => {
    let currentUserId = localStorage.getItem('mfnCurrentUser') ? JSON.parse(localStorage.getItem('mfnCurrentUser')).id : null;
    
    if (currentUserId) {
      //console.log(currentUserId)
      store.dispatch(id(currentUserId))
        .then(unwrapResult)
        .then(result => {
          if (!result.data.done) {
            navigate('/app/login');
          }
        });
    } else if (location.pathname !== '/app/' && !regAllowed.test(location.pathname)) {
      navigate('/app/login');
    }
  }, [cookies.Token, location.pathname, navigate, regAllowed]);



  return (
    <>
      <ThemeProvider theme={theme}>
        <CssBaseline/>
        <Routes>
          {/* APP */}
          <Route path='/app'           element={<Container/>}>
            {/* ACCOUNT MGMT */}
            <Route path='/app/login'     element={<Login/>}/>
            <Route path='/app/logout'    element={<Logout/>}/>
            <Route path='/app/register'  element={<Register/>}/>
            <Route path='/app/account-verify/:userId/:actionId'                      element={<AccountVerify/>}/>
            <Route path='/app/account-restore/:userId/:actionId/:verifyToken/:type'  element={<AccountRestore/>}/>
            <Route path='/app/account-restore/email-check'                           element={<AccountRestoreEmailCheck/>}/>
            {/* OTHER STUFF */}
            <Route path='/app/'                element={<MainPage/>}/>
            <Route path='/app/saved'           element={<SavedPosts/>}/>
            <Route path='/app/profile/:id'     element={<Profile/>}/>
            <Route path='/app/profile-edit'    element={<ProfileEdit/>}/>
            <Route path='/app/battles'         element={<Battles/>}/>
            <Route path='/app/post-upload'     element={<PostUpload/>}/>
            <Route path='/app/track/:id'       element={<Track/>}/>
            <Route path='/app/support'         element={<Support/>}/>
            <Route path='/app/f.a.q'           element={<FAQ/>}/>
            <Route path='/app/notifications'   element={<Notifications/>}/>
            <Route path='/app/*'               element={<NotFound404/>}/>
          </Route>
        </Routes>
      </ThemeProvider>
    </>
  );
}

export default App;

